/* 確認頁面的整體容器 */
.confirmationView {
    max-width: 800px;
    margin: 40px auto;
    padding: 20px;
    background-color: #ffffff; /* 可以根據需求更改背景色 */
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    font-family: "Lilita One", serif;
    color: #333333;
}

/* 確認信息列表 */
.confirmationView ul.customer-info {
    list-style-type: none;
    padding: 0;
    margin-bottom: 20px;
}

.confirmationView ul.customer-info li {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
}

.confirmationView ul.customer-info .info-label {
    width: 150px; /* 固定標籤寬度 */
    font-weight: bold;
    color: #2c3e50;
}

.confirmationView ul.customer-info .info-value {
    flex: 1; /* 內容部分自動填充剩餘空間 */
    color: #555555;
}

/* 確認表格樣式 */
.confirmation_table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
    table-layout: fixed;
}

.confirmation_table th,
.confirmation_table td {
    border: 1px solid #ddd;
    padding: 12px;
    word-wrap: break-word;
}

/* 標題列樣式 */
.confirmation_table th {
    background-color: rgba(66, 73, 80, 0.77); /* 深灰色 */
    color: #ffffff;
    font-size: 1.1em;
    text-align: left; /* 保持標題左對齊 */
}

/* 右對齊價格列 */
.confirmation_price-column {
    text-align: right;
}

/* 總計行樣式 */
.confirmation_table tr.total-row td {

}

/* 針對 Subtotal 行添加較粗的上邊框 */
.confirmation_table tr.subtotal-row td {

}

/* 總額行的金額右對齊 */
.confirmation_table tr.total-row td:last-child {
    text-align: right; /* 總額右對齊 */
}

/* 按鈕樣式 */
.confirmation-button {
    display: inline-block;
    padding: 10px 20px;
    margin-top: 20px;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.confirmation-button:hover {
    transform: translateY(-2px);
}

.confirmation-button:active {
    background-color: #1e8e47; /* 更深的綠色 */
    transform: translateY(0);
}
.link {
    text-decoration: none;
    color: inherit;
    transition: transform 0.2s ease; /* 平滑過渡效果 */
}

.link:hover {
    color: #8a949f;
    transform: translate(-90%) scale(1.3); /* 懸停時稍微放大 */
}




/* 響應式設計 */
@media (max-width: 600px) {
    .confirmationView {
        padding: 15px;
    }

    .confirmation_table th,
    .confirmation_table td {
        padding: 10px;
        font-size: 0.9em;
    }

    .confirmationView ul.customer-info li {
        font-size: 1em;
    }

    .confirmationView ul.customer-info .info-label {
        width: 100px; /* 在小屏幕上減小標籤寬度 */
    }

    .confirmationView ul.customer-info .info-value {
        font-size: 0.95em;
    }
}