
.category-buttons {
    display: flex;
    flex-direction: row;
    text-align: center;
    background-color: var(--neutral-color);
}

.button.selected-category-button {
    color: #E69500;
}

.button.unselected-category-button,
.button.unselected-category-button:visited {
    background-color: var(--neutral-color);
}

.button.unselected-category-button:hover,
.button.unselected-category-button:active {
    background-color: #E69500;
}



.category-buttons {
    display: flex;
    flex-direction: row;
    justify-content: center; /* 將導航列置中 */
    text-align: center;
    background-color: transparent; /* 背景設置為透明 */
    padding: 0; /* 移除多餘的空隙 */
}

.button.unselected-category-button,
.button.unselected-category-button:visited {
    background-color: transparent; /* 背景設置為透明 */
    color: var(--neutral-color); /* 預設字體顏色 */
}

.button.unselected-category-button:hover,
.button.unselected-category-button:active {
    color: #E69500; /* 懸停時字體顏色改為橘色 */
    background-color: transparent; /* 保持背景透明 */
}

.button.selected-category-button {
    background-color: transparent; /* 保持背景透明 */
    color: #E69500; /* 可自定義選擇狀態的顏色 */
    font-weight: bold;

}
.josefin-sans-title {
    font-family: "Josefin Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight: 700; /* Example weight */
    font-style: normal;

}

.josefin-sans-subtitle {
    font-family: "Josefin Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight: 700; /* Example weight */
    font-style: normal;
}

.josefin-sans-body {
    font-family: "Josefin Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight: 700; /* Example weight */
    font-style: normal;
}