.home-page {
    display: flex;
    background-color: #D6D6D6;
    flex-grow: 1;
    width: 100%;
    flex-wrap: wrap;
}

.welcome-text {
    width: 20em;
}

.category-image-items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1em;
}

.category-image-items a {
    text-align: center;
    cursor: pointer;
}

.category-image-items div {
    padding: 0.5em 0;
    background: rgba(0, 0, 0, 0.5);
    color: white;
    transform: translateY(-2.25em);
    margin-bottom: -2em;
}

.best-sellers, .new-arrive {
    background-color: #f1f1f1;
    width: 540px;
    height: 370px;
    padding: 20px 1px 30px 10px;
    border-radius: 10px;
    margin: 50px auto;
    display: inline-block;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    flex-wrap: wrap;
}

.book-list {
    display: flex;
    justify-content: center; /* 水平置中書本 */
    gap: 15px;
    flex-wrap: nowrap;
}

.book-item {
    text-align: center;
    width: 150px;
    height: auto;
    font-family: "Lilita One", serif;
    font-weight: 300; /* 字體變細 */
    color: #555555; /* 使用較淡的灰色 */
    margin: 0 auto; /* 確保項目在容器中置中 */
}

.book-item img {
    width: 140px;
    height: 200px;
}



.book-item p {
    margin-top: 10px;
    font-size: 14px;
    text-align: left;
    margin-left: 5px;
}

.book-item button {

    color: rgb(0, 0, 0);
    border: none;
    padding: 4px;
    border-radius: 15px;
    border: 1.5px solid #121212;
    cursor: pointer;
    margin-top: 10px;
    display: inline-flex;
}

.book-item button:hover {
    background-color: #e69500; /* 當 hover 時更改背景顏色 */
    color: #fff; /* 更改文字顏色 */
    font-weight: bold;
    border-color: #000000; /* 更改邊框顏色 */
}

.add-cart-icon {
    width: 16px !important;
    height: auto !important;
    margin-left: 2px;
    margin-right: 3px;
    display: inline-block;
}

.shop-now {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 40px 0;
    font-family: 'Lilita One', sans-serif;
    width: 100%;
}
.shop-now-button {
    background-color: #e69500;
    color: #424950;
    padding: 8px 40px;
    border: none;
    border-radius: 20px;
    font-size: 30px;
    cursor: pointer;
    margin-top: -40px;

}



.shop-now-button:hover {
    background-color: #e69500;
    color: #fff; /* 變更字體顏色 */
    transition: transform 0.2s ease, background-color 0.2s ease, color 0.2s ease;
    transform: scale(1.1);
}