body {
    display: flex;
    flex-direction: column;
    min-height: 100%;
    height: 800px;
}

main {
    flex-grow: 1;
    display: flex;
    width: 100%;
}

footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #191C1E;
    padding: 10px 20px;
    width: 100%;
    height: 50px;
}

.links {
    display: flex;
    gap: 20px;
}

.footer-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #333;
    color: white;
}

.links a {
    margin-right: 20px;
    text-decoration: none;
    color: white;
    text-decoration: none;
    color: #FFFFFF;
    padding: 10px;
}
.links a:hover {
    color: #cc7e00;
}

.social-media-icons a {
    margin-left: 20px;
    text-decoration: none;
    color: white;
}

.copyright {
    width: 100%;
    text-align: center;
    margin-top: 10px;
    font-size: 14px;
    color: #888;
}

.social-media-icons {
    display: flex;
    gap: 20px;
}

.social-media-icon {
    width: 60px;
    height: auto;
}