


/* 確保 cart-page 的 margin-bottom 為 0 */
.cart-page {
    font-family: "Lilita One", serif;
    background-color: #D6D6D6;
    margin-bottom: 0;
    padding-bottom: 0;
}

/* 標題樣式 */
.cart-page h1 {
    color: #3c3c3c;
    font-size: 2.5em;
    margin: 10px 0;
    text-align: center;
}

.cart-header {
    display: flex;
    align-items: center;
    justify-content: space-between; /* 將 h1 居中, 按鈕放在右側 */
    width: 100%;
    padding: 0 20px; /* 增加內邊距使布局更均勻 */
}

.cart-header h1 {
    flex-grow: 1; /* 讓 h1 在容器中占據中間空間 */
    text-align: center;
}

/* 總價與按鈕的容器 */
.cart-summary-container {
    text-align: center;
    margin-top: 20px;

}

/* 總價樣式 */
.cart-total {
    font-size: 1.2rem;
    margin-bottom: 10px;
}

/* 按鈕容器樣式 */
.cart-buttons, .clear-cart-container {
    display: flex;
    justify-content: center; /* 將按鈕置中 */
    gap: 10px; /* 按鈕之間的間距 */
    margin-top: 10px; /* 與表格稍微分開 */
}

/* 一般按鈕樣式 */
.keepshop{
    color: rgb(0, 0, 0);
    border: none;
    font-size: 20px;
    border-radius: 4px;
    border: 1px solid #121212;
    cursor: pointer;
    padding: 4px 8px;
    margin-top: 10px;
    margin-left: 10px;
    transition: transform 0.2s ease, background-color 0.2s ease, color 0.2s ease;
}

.keepshop:hover {
    background-color: #808080; /* 當 hover 時更改背景色 */
    color: #fff; /* 變更字體顏色 */
    font-weight: bold; /* 使字體變粗 */
    transform: scale(1.03);

}

.clear-cart-button{
    color: rgb(0, 0, 0);
    border: none;
    font-size: 12px;
    border-radius: 4px;
    border: 1px solid #121212;
    cursor: pointer;
    padding: 4px 8px;
}
.checkout-button {
    color: #cc7e00;
    border: none;
    font-size: 12px;
    border-radius: 1px;
    border: 1px solid #b1b1b1;
    cursor: pointer;
    padding: 4px 8px;
    font-size: 18px; /* 調整 Checkout 按鈕的字體大小 */
    padding: 14px 28px; /* 調整 Checkout 按鈕的內邊距 */
    border-radius: 6px;
    transition: transform 0.15s ease, background-color 0.15s ease, color 0.15s ease;
    margin-bottom: 10px;
}

.clear-cart-button:hover {
    background-color: #808080; /* 當 hover 時更改背景色 */
    color: #fff; /* 變更字體顏色 */

    transition: transform 0.2s ease, background-color 0.2s ease, color 0.2s ease;
}

/* Checkout 按鈕的特定樣式 */


/* Checkout 按鈕懸停時的字體顏色 */
.checkout-button:hover {
    background-color: #cc7e00; /* 當 hover 時更改背景色 */
    color: #fff; /* 變更字體顏色 */
    transform: scale(1.2);
}