.header-dropdown {
    position: relative;
}

.categories-button {
    font-size: 20px;
    margin-top: 0.25em;
    display: flex;
    align-items: center;
    gap: 4px;
    transition: color 0.2s ease;
}

.header-dropdown ul {
    background-color: white;
    color: black;
    position: absolute;
    min-width: 8em;
    z-index: 1;
    cursor: pointer;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-10px);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    border-radius: 4px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
}

.header-dropdown li {
    padding: 0.25em 0.5em;
    transition: all 0.2s ease;
}

.header-dropdown a {
    color: black;
    text-decoration: none;
    display: block;
    transition: color 0.2s ease; /* 添加顏色過渡效果 */
}

.header-dropdown li:hover {
    background: #dd9006;
    padding-left: 1em;
}

.header-dropdown li:hover a {
    color: white; /* hover 時改變連結顏色為白色 */
}

.header-dropdown:hover ul {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}

.header-dropdown-and-cart {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 10px;
    width: 100%;
}

.cart-and-categories {
    display: flex;
    gap: 10px;
}

.button {
    background: none;
    border: none;
    text-align: center;
    color: #F5F0F0;
    cursor: pointer;
    font-size: inherit;
    text-decoration: none;
    transition: color 0.2s ease;
}

.button:hover {
    color: white;
    background: none;
}

@media (max-width: 600px) {
    .header-dropdown ul {
        position: fixed;
        top: auto;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        transform: translateY(100%);
        border-radius: 16px 16px 0 0;
    }

    .header-dropdown:hover ul {
        transform: translateY(0);
    }

    .header-dropdown li {
        padding: 1em;
        text-align: center;
    }

    .header-dropdown li:hover {
        padding-left: 1em;
    }
}