/* CartTable 基本樣式 */
.cart-table {
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    border-collapse: collapse;
    background-color: aliceblue;
    table-layout: fixed;


}

.cart-table th {
    background-color: rgba(66, 73, 80, 0.77); /* 淺抹綠色 */
    color: #FFFFFF;
    padding: 0.5em;
    border-bottom: 1px solid #ddd;
    text-align: center;
}

.cart-table td {
    background-color: #F7F7F5; /* 米白色 */
    padding: 0.5em;
    border-bottom: 1px solid #ddd;
    text-align: center;
}

/* 設置各個 column 的寬度 */

.cart-table td:nth-child(1) {
    width: 15%; /* Book 圖片列 */
}


.cart-table td:nth-child(2) {
    width: 25%; /* Book 標題列 */
}


.cart-table td:nth-child(3) {
    width: 15%; /* Price 列 */
}


.cart-table td:nth-child(4) {
    width: 20%; /* Quantity 列 */
}


.cart-table td:nth-child(5) {
    width: 15%; /* Amount 列 */
}


.cart-table td:nth-child(6) {
    width: 10%; /* Remove 列 */
}

/* 書籍圖片樣式 */
.cart-book-image img {
    max-width: 60px; /* 縮小圖片寬度 */
    height: auto;
}

/* 標題樣式 */
.cart-book-title {
    font-weight: bold;
    font-size: 1rem;
    color: #333;
}

/* 按鈕樣式 */
.icon-button {
    border: none;
    background: none;
    cursor: pointer;
}

.inc-button {
    font-size: 1.05rem;
    color: var(--primary-color);
    transition: transform 0.1s ease, background-color 0.1s ease, color 0.1s ease;
}
.inc-button:hover {
    transform: scale(1.15);
}

.dec-button {
    font-size: 1.05rem;
    color: #ccc;
    transition: transform 0.1s ease, background-color 0.1s ease, color 0.1s ease;
}

.dec-button:hover {
    transform: scale(1.15);
}

.remove-button {
    color: #7c7c7c;
}

.remove-button:hover {
    color: #cd3d3d;
}
/* 空購物車訊息樣式 */
.empty-cart-message {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 150px; /* 設定一個適當的高度來居中 */
    font-size: 1.5rem;
    color: #666;
    text-align: center;
}

