#book-boxes {

    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 1em;
    gap: 1em;
    max-width: 900px;
    margin: 0 auto;
}
.category-page{
    background-color:#D6D6D6;
}

.book-image {
    width: 110px;
    height: 150px;
    margin-bottom: 10px;
    position: relative;
}

.book-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.book-image .button {
    position: absolute;
    bottom: 5px;
    left: 90%;
    transform: translate(-90%);
    background-color: rgba(223, 223, 223, 0.9);
    color: rgb(24, 24, 24);
    border: none;
    font-size: 10px;
    cursor: pointer;
    border-radius: 10px;
    padding: 5px 20px;
    white-space: nowrap;
    transition: transform 0.2s ease; /* 平滑過渡效果 */
}

.book-image .button:hover {
    transform: translate(-90%) scale(1.05); /* 懸停時稍微放大 */
}

.book-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: calc(25% - 20px);
    height: auto;
    font-family: "Lilita One", serif;
    margin-bottom: 10px;
    padding: 10px;
    box-sizing: border-box;
    background-color: #E9E9E9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    font-weight: 100; /* 字體變細 */
    color: #555555;
    font-size: 0.8em;
}

@media (max-width: 768px) {
    .book-box {
        width: calc(33.33% - 20px);
    }
}

@media (max-width: 600px) {
    .book-box {
        width: calc(50% - 20px);
    }
}


@media (max-width: 400px) {
    .book-box {
        width: 100%;
    }
}


.book-title {
    font-size: 14px;
    margin-bottom: 5px;
    text-align: center; /* 文字置中 */
    white-space: normal; /* 允許換行 */
    word-wrap: break-word; /* 自動換行 */
    line-height: 1.2em; /* 控制行距 */
}

.book-author {
    font-style: italic;
    margin-bottom: 2px;
    text-align: left;
}



.book-box button {
    color: rgb(0, 0, 0);
    border: none;
    padding: 4px;
    border-radius: 8px;
    border: 1.5px solid #121212;
    cursor: pointer;
    margin-top: 10px;
    display: inline-flex;
}

.add-cart-icon {
    width: 12px !important;

    margin-left: 2px;
    margin-right: 3px;
    display: inline-block;
}


.book-price-and-cart {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 5px;
    margin-top: 0;
}

.book-price {
    padding-top: 10px;
    font-size: 14px;
    color: #333;

}


